body, html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

#particles-js {
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
  height: 100%;
}

main {
  position: relative;
  z-index: 1;
  width: 80%;
  margin: 10rem auto;
  padding-bottom: 10rem;
  box-sizing: border-box;
}

.infoContainer {
  margin: 0 auto;
  padding: 2rem 4rem;
  text-align: center;
  border-radius: 4rem;
  background-color: #343a40;
}
.infoContainer .info__tittle {
  font-size: 3.6rem;
  color: #f8f9fa;
  font-family: "DM Sans", sans-serif;
}
.infoContainer .info__tittle span {
  color: #f5cb5c;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 4.6rem;
}
.infoContainer .info__equipos {
  font-size: 2rem;
  color: #f8f9fa;
}
.infoContainer .info__slogan {
  font-size: 1.8rem;
  font-family: "Caveat", cursive;
  color: #f8f9fa;
}

.tableControl {
  max-width: 100%;
  margin: 2rem auto;
  text-align: center;
  justify-content: space-evenly;
  align-items: center;
  gap: 2rem;
  padding: 4rem 4rem;
  font-size: 1.6rem;
  border-radius: 2rem;
  background-color: #dee2e6;
}
@media (min-width: 768px) {
  .tableControl {
    max-width: 85%;
  }
}
.tableControl .label {
  font-size: 2.6rem;
  color: #212529;
  font-family: "DM Sans", sans-serif;
  font-weight: bold;
}
@media (min-width: 768px) {
  .tableControl .label {
    font-size: 2rem;
  }
}
.tableControl .control {
  margin-bottom: 2rem;
}
.tableControl .control .ipContainer {
  display: flex;
  align-items: center;
}
.tableControl .control .ipContainer input {
  width: 100%;
  height: 3rem;
  border-radius: 1.6rem;
}
.tableControl .control .ipContainer #ip1, .tableControl .control .ipContainer #ip2, .tableControl .control .ipContainer #ip3, .tableControl .control .ipContainer #ip4 {
  outline: none;
  border: 1px solid #111111;
}
.tableControl .flex {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 1024px) {
  .tableControl .flex {
    gap: 4rem;
    flex-direction: row;
  }
}

.tableContainer {
  max-width: 100%;
  max-height: 60rem;
  overflow-y: auto;
  overflow-x: auto;
  margin: 4rem auto;
  text-align: center;
  border-radius: 1rem;
  background-color: #dee2e6;
  padding: 2rem 1rem;
  font-size: 1.6rem;
}
.tableContainer table {
  border: solid 1px #111111;
  border-collapse: collapse;
