@keyframes spark-1 {
  to {
    stroke-dashoffset: -1000;
  }
}
@keyframes spark-2 {
  to {
    stroke-dashoffset: -500;
  }
}
@keyframes fly-up {
  0% {
    opacity: 0;
    transform: translateY(0) scale(0.2);
  }
  5% {
    opacity: 1;
    transform: translateY(-1.5rem) scale(0.4);
  }
  10%, 100% {
    opacity: 0;
    transform: translateY(-3rem) scale(0.2);
  }
}
@keyframes fly-down {
  0% {
    opacity: 0;
    transform: translateY(0) scale(0.2);
  }
  5% {
    opacity: 1;
    transform: translateY(1.5rem) scale(0.4);
  }
  10%, 100% {
    opacity: 0;
    transform: translateY(3rem) scale(0.2);
  }
}